
.rd-alert-container {
    position: relative;
}
.rd-alert {
    position: relative;
    color: #666;
    line-height: 1rem;
    margin-bottom: .2rem;
    padding: .5rem 1rem .5rem 3rem;
}
.rd-alert .rd-alert-icon {
    font-size: 2rem;
    top: 50%;
    left: .5rem;
    margin-top: -1rem;
}
.rd-alert .rd-alert-title {
    font-size: 1rem;
}
.rd-alert .rd-alert-content {
    margin: .5rem 0 0 0;
    font-size: .8rem;
}
.rd-alert.small {
    padding: 0;
}
.rd-alert.small .rd-alert-title {
    font-size: .8rem;
    line-height: 2rem;
    padding-left: 2rem;
}
.rd-alert.small .rd-alert-icon {
    line-height: 2rem;
    left: .5rem;
    font-size: 1rem;
    top: initial;
    margin-top: 0;
}
.rd-alert.success {
    border: 1px solid #e7f6e1;
    border-left: 2px solid #87d068;
    background-color: #f3faf0;
}
.rd-alert.success .rd-alert-icon {
    color: #87d068;
}
.rd-alert.info {
    border: 1px solid #d5f1fd;
    border-left: 2px solid #2db7f5;
    background-color: #eaf8fe;
}
.rd-alert.info .rd-alert-icon {
    color: #2db7f5;
}
.rd-alert.warning {
    border: 1px solid #fec;
    border-left: 2px solid #fa0;
    background-color: #fff7e6;
}
.rd-alert.warning .rd-alert-icon {
    color: #fa0;
}
.rd-alert.failed {
    border: 1px solid #fdc;
    border-left: 2px solid #f50;
    background-color: #ffeee6;
}
.rd-alert.failed .rd-alert-icon {
    color: #f50;
}
.rd-alert-icon {
    position: absolute;
}



