
.rd-upload-container {
    display: inline-block;
}
.rd-upload-wrapper {
}
.rd-upload-file {
    display: none;
}
.rd-upload-preview {
    display: -ms-flexbox;
    display: flex;
}
.rd-upload-list-item {
    position: relative;
    width: 5rem;
    height: 8rem;
    margin: .5rem
}
.rd-upload-list-item:hover {
    color: #57c5f7;
}
.rd-upload-list-item:hover .rd-upload-preview-action {
    opacity: 1;
}
.rd-upload-list-item:hover .rd-upload-preview-action .rd-upload-close {
    display: inline;
}
.rd-upload-list-item:first-child {
    margin-left: 0;
}
.rd-upload-preview-img-wrapper {
    display: inline-block;
    height: 5rem;
    width: 5rem;
    border: 1px solid #e0e0e0;
    border-radius: .4rem;
    padding: .5rem;
    box-sizing: border-box;
}
.rd-upload-preview-img {
    width: 100%;
    height: 100%;
    background-size: cover;
}
.rd-upload-preview-info {
    display: inline-block;
    vertical-align: top;
    font-size: .8rem;
    width: 100%;
    text-align: center;
    overflow: hidden;
}
.rd-upload-preview-info-title, .rd-upload-preview-info-size {
    height: 1rem;
    overflow: hidden;
    text-align: center;
}
.rd-upload-preview-action {
    position: absolute;
    width: 100%;
    height: 5rem;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.29);
    border-radius: .4rem;
    opacity: 0;
    transition: opacity .3s;
}
.rd-upload-close {
    display: none;
    color: #fff;
    font-size: 1.5rem;
    line-height: 5rem;
}



