.rd-select-container {
    position: relative;
    border: 1px solid #d9d9d9;
    display: inline-block;
    min-width: 6rem;
    border-radius: 6px;
    height: 2rem;
    line-height: 2rem;
    padding: 0 2rem 0 .5rem;
    vertical-align: bottom;
    box-sizing: border-box;
    width: 100%;
}
.rd-select-container:hover {
    border: 1px solid #2db7f5;
}
.rd-select-container.disabled {
    background: #f4f5f9;
    color: #666;
}
.rd-select-container.disabled:hover {
    border: 1px solid #d9d9d9;
}
.rd-select-arrow {
    position: absolute;
    top: 0;
    right: .5rem;
    font-size: .4rem;
    transform: rotate(0);
    transition: transform 0.2s ease;
}
.rd-select-container.active .rd-select-options-container {
    display: initial;
    max-height: 20rem;
    overflow-y: auto;
    overflow-x: hidden;
}
.rd-select-container.active .rd-select-arrow {
    transform: rotate(180deg);
}
.rd-select-options-container {
    position: absolute;
    min-width: 6rem;
    padding: 0;
    top: 2.2rem;
    left: -1px;
    background-color: #fff;
    z-index: 2;
    box-shadow: rgba(0, 0, 0, 0.117647) 0px 1px 6px, rgba(0, 0, 0, 0.117647) 0px 1px 4px;
    border-radius: 2px;
    max-height: 20rem;
    overflow-y: auto;
}
.rd-select-option {
    padding: 0 .5rem;
    font-size: .8rem;
    padding: .1rem .5rem;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: all .3s ease;
}
.rd-select-option.selected {
    background: #67cdfb;
    color: #fff;
}
.rd-select-option.disabled {
    background: #efefef;
    color: #b7b7b7;
    cursor: not-allowed;
}
.rd-select-option:hover {
    opacity: .7;
}
.rd-select-top .rd-select-options-container{
    bottom: 2rem;
    top: initial;
}
.rd-select-selected-value {
    position: relative;
    font-size: .8rem;
    line-height: 2rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.rd-select-search-wrapper {
    width: 100%;
    box-sizing: border-box;
}
.rd-select-search-input {
    width: 100%;
    border: 0;
    font-size: 14px;
    opacity: .5;
    outline: none;
    padding: .5rem;
    line-height: .8rem;
    box-sizing: border-box;
}
@keyframes pickerFadeInDown {
from {
    opacity: 0;
    transform: translate3d(0, -1rem, 0);
}
to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}
}
@keyframes pickerFadeOutUp {
from {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}
to {
    opacity: 0;
    transform: translate3d(0, -1rem, 0);
}
}
.picker-fade-in-down-transition {
  animation-duration: .2s;
  animation-fill-mode: both;
}
.picker-fade-in-down-enter {
    animation-name: pickerFadeInDown;
}
.picker-fade-in-down-leave {
    animation-name: pickerFadeOutUp;
}



