
.rd-preview-container {
    display: inline-block;
    padding: 1rem;
    position: fixed;
    left: 0;
    top: 0;
    text-align: center;
    width: 100%;
    height: 100%;
}
.rd-preview-wrapper {
    position: fixed;
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    background: rgba(0,0,0,0.8);
    height: 100%;
    box-sizing: border-box;
    text-align: center;
}
.rd-preview-close {
    position: absolute;
    right: .5rem;
    top: .5rem;
}
.rd-preview-img {
    max-width: 100%;
    max-height: 100%;
}
.rd-preview-close {
    position: absolute;
    right: .6rem;
    top: .5rem;
    font-size: 1rem;
    background: rgba(0, 0, 0, 0.54);
    color: #fff;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    line-height: 1.5rem;
    text-align: center;
    z-index: 2;
}
.rd-preview-nav-left, .rd-preview-nav-right {
    position: absolute;
    height: 100%;
    margin: .25rem;
    width: 10rem;
    top: 0;
    color: #fff;
    transition: opacity .2s;
}
.rd-preview-nav-left .rd-preview-nav-arrow, .rd-preview-nav-right .rd-preview-nav-arrow {
    transition: background .3s;
}
.rd-preview-nav-left:hover, .rd-preview-nav-right:hover {
    opacity: 1;
    transition: opacity .2s;
}
.rd-preview-nav-left:hover .rd-preview-nav-arrow, .rd-preview-nav-right:hover .rd-preview-nav-arrow {
    background: rgba(0, 0, 0, 0.53);
}
.rd-preview-nav-left {
    left: 0;
}
.rd-preview-nav-left .rd-preview-nav-arrow {
    left: 0;
    margin-left: 2rem;
}
.rd-preview-nav-right {
    right: 0;
}
.rd-preview-nav-right .rd-preview-nav-arrow {
    right: 0;
    margin-right: 2rem;
}
.rd-preview-nav-arrow {
    font-size: 3rem;
    position: absolute;
    top: 50%;
    margin-top: -2.5rem;
    padding: 0 .5rem;
    background: rgba(0, 0, 0, 0);
    line-height: 2rem;
    border-radius: .2rem;
}
.rd-preview-title {
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    color: #fff;
    background: rgba(0,0,0,.5);
    box-sizing: border-box;
    font-size: 2rem;
}
@media all and (max-width: 768px) {
.rd-preview-wrapper {
        top: 0;
        z-index: 10000;
        width: 100%;
        height: 100%;
        margin: 0;
        left: 0;
}
.rd-preview-nav-left, .rd-preview-nav-right {
        width: 5rem;
}
.rd-preview-nav-arrow {
        font-size: 2.5rem;
}
.rd-preview-nav-left .rd-preview-nav-arrow {
        margin-left: 0;
}
.rd-preview-nav-right .rd-preview-nav-arrow {
        margin-right: 0;
}
}



