
.rd-cascader-container {
    position: relative;
    border: 1px solid #d9d9d9;
    display: inline-block;
    min-width: 6rem;
    border-radius: 6px;
    height: 2rem;
    line-height: 2rem;
    padding: 0 2rem 0 .5rem;
    vertical-align: bottom;
}
.rd-cascader-container:hover {
    border: 1px solid $primary-color;
}
.rd-cascader-input {
    display: inline-block;
}
.rd-cascader-text {
    font-size: .8rem;
    line-height: 2rem;
}
.rd-cascader-content {
    position: absolute;
    top: 2rem;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    border: 1px solid #e8e8e8;
    border-radius: .25rem;
    height: 10rem;
    background: #fff;
    box-shadow: 0 1px 6px hsla(0,0%,39%,.2);
    z-index: 2;
}
.rd-cascader-menu {
    min-width: 6rem;
    border-right: 1px solid #e8e8e8;
    overflow-y: auto;
}
.rd-cascader-item {
    font-size: .8rem;
    padding: .1rem .5rem;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: all .3s ease;
}
.rd-cascader-item:hover, .rd-cascader-item.active {
    background: #efefef;
}
.rd-cascader-arrow {
    position: absolute;
    top: 0;
    right: .5rem;
    font-size: .4rem;
    transform: rotate(0);
    transition: transform 0.2s ease;
}
.rd-cascader-container.active .rd-cascader-arrow {
    transform: rotate(180deg);
}
.rd-cascader-top .rd-cascader-content {
    bottom: 2rem;
    top: initial;
}



