
.rd-checkbox-wrapper {
    display: inline-block;
    cursor: pointer;
    font-size: 1rem;
    line-height: 2rem;
}
.rd-checkbox-input {
    opacity: 0;
}
.rd-checkbox-inner {
    position: relative;
    display: inline-block;
    height: 1rem;
    width: 1rem;
    border: 1px solid #e6e6e6;
    border-radius: 4px;
    top: .5rem
}
.rd-checkbox-inner:hover {
    border: 1px solid #2db7f5;
}
.rd-checkbox-inner-icon {
    position: absolute;
    color: #fff;
    font-size: .8rem;
    left: 50%;
    top: 50%;
    width: .8rem;
    height: .8rem;
    margin-top: -.3rem;
    margin-left: -.4rem;
    line-height: .8rem;
    opacity: 0;
    text-align: center;
    transition: opacity .2s;
}
.rd-checkbox-inner.selected {
    background: #2db7f5;
    border: 1px solid #2db7f5
}
.rd-checkbox-inner.selected:hover {
    opacity: 0.8;
}
.rd-checkbox-inner.selected .rd-checkbox-inner-icon{
    opacity: 1;
}
.rd-checkbox-inner.disabled {
    background: #eaeaea;
}



