
.rd-btn {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    border: 1px solid transparent;
    white-space: nowrap;
    padding: .3rem;
    font-size: .8rem;
    line-height: 1.5;
    min-width: 5rem;
    border-radius: 4px;
    transition: background 0.2s;
    outline: none;
    color: #666;
    background-color: #fff;
    border-color: #d9d9d9;
}
.rd-btn i, .rd-btn .rd-btn-loading {
    margin-right: .3rem;
}
.rd-btn.default {
    color: #666;
    background-color: #fff;
    border-color: #d9d9d9;
}
.rd-btn.primary {
    color: #fff;
    background-color: #57c5f7;
    border-color: #57c5f7
}
.rd-btn.primary:hover, .rd-btn.primary:focus {
    background-color: #81d8ff;
    color: #fff;
}
.rd-btn.ghost {
    color: #666;
    background-color: #f7f7f7;
    border-color: #d9d9d9;
}
.rd-btn.warning {
    color: #fff;
    background-color: #fa0;
    border-color: #fa0
}
.rd-btn.warning:hover, .rd-btn.warning:focus {
    color: #fff;
    background-color: rgba(255, 170, 0, 0.7);
    border-color: #fa0;
}
.rd-btn.danger {
    color: #fff;
    background-color: #f50;
    border-color: #f50
}
.rd-btn.danger:hover, .rd-btn.danger:focus {
    color: #fff;
    border-color: #f50;
    background-color: rgba(255, 85, 0, 0.7);
}
.rd-btn.success {
    background-color: #87d068;
    border-color: #87d068;
    color: #fff
}
.rd-btn.success:hover, .rd-btn.success:focus {
    color: #fff;
    border-color: #87d068;
    background-color: rgba(135, 208, 104, 0.7);
}
.rd-btn.info {
    color: #fff;
    border-color:#2db7f5;
    background-color: #2db7f5
}
.rd-btn.info:hover, .rd-btn.info:focus {
    color: #fff;
    border-color: #2db7f5;
    background-color: rgba(45, 183, 245, 0.7);
}
.rd-btn:focus, .rd-btn:hover {
    color: #57c5f7;
    background-color: #fff;
    border-color: #57c5f7;
}
.rd-btn.rd-btn-small {
    padding: .2rem .3rem;
    min-width: initial;
    font-size: .8rem;
    line-height: 1rem;
    min-width: initial;
}
.rd-btn.rd-btn-large {
    padding: .2rem 1.5rem;
    font-size: 1.4rem;
}
.rd-btn.rd-btn-icon {
    width: 2rem;
    min-width: initial;
    border-radius: 50%;
    height: 2rem;
    line-height: 2rem;
    padding: 0;
}
.rd-btn.rd-btn-icon i {
    margin-right: 0;
}
@keyframes loading {
from {
    transform-origin: 50% 50%;
    transform: rotate(0deg);
}
to {
    transform-origin: 50% 50%;
    transform: rotate(360deg);
}
}
.rd-btn-loading {
    display: inline-block;
    animation: loading 1s infinite linear;
}
.rd-btn.rd-btn-disabled {
    color: #c3c3c3;
    background: #ececec;
    border-color: #cecece;
    cursor: not-allowed
}
.rd-btn.rd-btn-disabled:hover {
    color: #a7a7a7;
    background: #ececec;
    border-color: #d9d9d9;
}
.rd-btn-icon-only {
    font-size: 1rem;
    line-height: 2rem;
    vertical-align: baseline;
}



