
.rd-textfield-input {
    font-size: .9rem;
    line-height: 1.7rem;
    color: #666;
    background-color: #fff;
    background-image: none;
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    padding: .1rem .5rem;
    width: 100%;
    outline: none;
    box-sizing: border-box;
    transition: border-color .3s ease-in;
}
.rd-textfield-input:focus, .rd-textfield-input:hover {
    border-color: #57c5f7;
}
.rd-textfield-wrapper {
    position: relative;
    width: 100%
}
.rd-textfield-wrapper.disabled .rd-textfield-input {
    color: #888888;
    background-color: #f9f9f9;
    border-color: #e7e7e7;
}
.rd-textfield-wrapper.loading .rd-textfield-icon {
    animation: loading 1s infinite linear;
    color: #797979;
}
.rd-textfield-wrapper.warning .rd-textfield-icon {
    color: #fa0;
}
.rd-textfield-wrapper.warning .rd-textfield-input {
    border-color: #fa0;
}
.rd-textfield-wrapper.failed .rd-textfield-icon {
    color: #f50;
}
.rd-textfield-wrapper.failed .rd-textfield-input {
    border-color: #f50;
}
.rd-textfield-wrapper.success .rd-textfield-input {
    border: 1px solid #87d068;
}
.rd-textfield-wrapper.success .rd-textfield-icon {
    color: #87d068;
}
.rd-textfield-wrapper.info .rd-textfield-input {
    border: 1px solid #2db7f5;
}
.rd-textfield-wrapper.info .rd-textfield-icon {
    color: #2db7f5;
}
.rd-textfield-wrapper.inline {
    display: inline-block;
    width: auto;
}
@keyframes loading {
from {
    transform-origin: 50% 50%;
    transform: rotate(0deg);
}
to {
    transform-origin: 50% 50%;
    transform: rotate(360deg);
}
}
.rd-textfield-icon {
    display: inline-block;
    position: absolute;
    right: .5rem;
    top: 50%;
    margin-top: -.5rem;
    height: 1rem;
    line-height: 1rem;
    font-size: 1rem;
}
.rd-textfield-tip {
    position: absolute;
    left: 0;
    bottom: -1rem;
    font-size: .8rem;
    line-height: .8rem;
}



