
.rd-modal-mask {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #373737;
    background-color: rgba(55,55,55,.6);
    height: 100%;
    z-index: 990;
    filter: alpha(opacity=50);
}
.rd-modal-wrapper {
    position: fixed;
    overflow: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000;
    -webkit-overflow-scrolling: touch;
    outline: 0;
}
.rd-modal {
    position: relative;
    width: auto;
    margin: 0 auto;
    top: 100px;
    width: 30rem;
    background-color: #fff;
    border-radius: .25rem
}
.rd-modal.large {
    width: 45rem;
}
.rd-modal-header {
    font-size: 1.2rem;
    padding: 1rem 1rem;
    border-bottom: 1px solid #f3f3f3;
    color: #545454;
}
.rd-modal-body {
    padding: 1rem;
    border-bottom: 1px solid #f3f3f3;
    min-height: 7rem;
}
.rd-modal-footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding: .5rem 1rem;
}
.rd-modal-footer .rd-btn {
    margin-left: .5rem;
}
.rd-modal-header-close {
    font-size: 1rem;
    position: absolute;
    right: 1rem;
}
@media screen and (max-width: 768px) {
.rd-modal {
        width: 90%;
}
.rd-modal-body {
        min-height: 4rem;
}
}
@keyframes modalFadeInDown {
from {
    opacity: 0;
    transform: translate3d(0, -50%, 0);
}
to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}
}
@keyframes modalFadeOutUp {
from {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}
to {
    opacity: 0;
    transform: translate3d(0, -50%, 0);
}
}
.modal-fade-in-down-enter {
  animation-duration: .2s;
  animation-fill-mode: both;
}
.modal-fade-in-down-enter-active {
    animation-name: modalFadeInDown;
}
.modal-fade-in-down-leave-active {
    animation-name: modalFadeOutUp;
}



