.rd-page{
    font-size: 12px;
}
.rd-page .item {
    cursor: pointer;
    border-radius: 6px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    min-width: 28px;
    height: 28px;
    line-height: 28px;
    text-align: center;
    list-style: none;
    float: left;
    border: 1px solid #d9d9d9;
    background-color: #fff;
    margin-right: 8px;
    font-family: Arial;
}
.rd-page .item span{
    margin: 0 6px;
    color: #666;
}
.rd-page .item:hover{
    border-color: #2db7f5;
}
.rd-page .item:hover span{
    color: #2db7f5;
}
.rd-page .item.active{
    background-color: #2db7f5;
    border-color: #2db7f5;
}
.rd-page .item.active span{
    color: #fff;
}
.rd-page .item.disabled{
    cursor: not-allowed;
    border-color: #d9d9d9;
}
.rd-page .item.disabled span{
    color: #ccc;
}
.rd-page .jump-prev, .rd-page .jump-next{
    font-family: Arial;
    cursor: pointer;
    color: #666;
    border-radius: 6px;
    list-style: none;
    min-width: 28px;
    height: 28px;
    line-height: 28px;
    float: left;
    text-align: center;
    transition: all .3s ease;
    display: inline-block;
    margin-right: 8px
}
.rd-page .jump-prev:after, .rd-page .jump-next:after{
    content: "\2022\2022\2022";
    display: block;
    letter-spacing: 2px;
    color: #ccc;
    text-align: center;
}
.rd-page .jump-prev:hover{}
.rd-page .jump-prev:hover:after{
    content: "\f3d2\f3d2";
    color: #2db7f5;
    display: inline-block;
    zoom: 1;
    letter-spacing: 1px;
    font-family: "Ionicons";
}
.rd-page .jump-next:hover{}
.rd-page .jump-next:hover:after{
    content: "\f3d3\f3d3";
    color: #2db7f5;
    display: inline-block;
    zoom: 1;
    letter-spacing: 1px;
    font-family: "Ionicons";
}
.rd-page:after{
    display: table;
    content: '';
    height: 0;
    clear: both;
    overflow: hidden;
}



